// Define Titillium Web fonts with @font-face
@font-face {
    font-family: 'Titillium Web';
    src: url('../../assets/fonts/TitilliumWeb-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Titillium Web';
    src: url('../../assets/fonts/TitilliumWeb-Italic.ttf') format('truetype');
    font-weight: normal;
    font-style: italic;
}

